
export default {
	onLaunch: function() {
		this.getIndexaction()





	},
	onShow: function() {
		// console.log('App Show')
	},
	onHide: function() {
		// console.log('App Hide')
	},
	methods: {
		getIndexaction() {
			this.$api.getIndexaction(this.$store.state.openId).then(res => {
				if (res.status === 200) {
					this.$store.state.indexaction = res.data
				}
			}).catch(err => {
				console.log(err)
			})
		}
	},
}
